a {
  text-decoration: none;
  color: black;
}
.App {
  text-align: center;
}

.Nav-Bar {
  display: flex;
  justify-content: space-between;
  width: 60%;
  text-align: center;
  margin-left: 20%;
  margin-top: 2rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 0.7rem;
  position: sticky;
}

.link:hover {
  color: #28a9ff;
}

.logo {
  width: 3rem;
  margin-top: -1.25rem;
}

.Landing-Intro {
  position: absolute;
  width: 28rem;
  left: 20%;
  top: 6rem;
}

.Name {
  font-size: 2.4rem;
  color: black;
  text-align: left;
  left: 3rem;
  line-height: 4rem;
  font-family: 'Gelasio', serif;
  font-weight: 600;
}

.Intro {
  font-size: 0.8rem;
  text-align: left;
  font-family: 'Open Sans', sans-serif;
  font-weight: 200;
}

.Iselective {
  width: 60%;
  margin-left: -5rem;
  margin-top: 24rem;
  margin-bottom: 4rem;
  filter: drop-shadow(0 0.75rem 0.75rem rgb(94, 94, 94, 0.5));
}

.Cozone {
  width: 60%;
  margin-left:5rem;
  margin-bottom: 4rem;
  filter: drop-shadow(0 0.75rem 0.75rem rgb(94, 94, 94, 0.5));
}

.Navindor {
  width: 60%;
  margin-left: -5em;
  margin-bottom: 4rem;
  filter: drop-shadow(0 0.75rem 0.75rem rgb(94, 94, 94, 0.5));
}

.project:hover {
  transform: scale(1.1);
  opacity: 0.6;
}

.greeting {
  margin-top: 4rem;
  font-size: 1.5rem;
  color: black;
  text-align: center;
  font-family: 'Noto Serif', serif;
  font-weight: 600;
}

.footer {
  position: absolute;
  justify-content: space-between;
  width: 100%;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 0.7rem;
  /* background-color: rgb(142, 196, 241); */
  background-color: rgb(136, 227, 255);
}

.icon {
  color:rgb(255, 187, 1);
  margin-right: 1rem;
  margin-bottom: 1rem;
}

.icon:hover{
  color: rgb(6, 142, 253);
}

.copynote {
  font-size: 0.6rem;
  margin-bottom: 4rem;
}

.flex {
  display: flex;
  justify-content: space-evenly;
}

.topMargin {
  margin-top: 4rem;
}

.half {
  width: 40vw;
  height: auto;
  font-family: 'Open Sans', sans-serif;
  font-size: 0.8rem;
}

.title {
  font-weight: bold;
  font-size: 36px;
}

.boldSpan {
  font-weight: bold;
}

.centerButton {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.buttonText:hover {
  color:white;
  background-color: #28a9ff;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  border-radius: 2rem;
}

.buttonText {
  color: #28a9ff;
  background-color: white;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  border-radius: 2rem;
  border: solid 0.5px  #28a9ff;
}


.centerText1 {
  text-align: center;
  font-family: 'Noto Serif', serif;
  font-size: 0.8rem;
}



html { /* mobile */
  font-size:16px;
}

@media only screen and (min-width: 600px){
  html { /* tablet */
      font-size:19px;
  }
}
@media only screen and (min-width: 768px){
  html { /* desktop */
      font-size:22px;
  }
}